define('my-new-app/controllers/users/edit', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports['default'] = Ember['default'].Controller.extend({
    actions: {
      save: function save() {
        this.get('model').save();
      },
      remove: function remove() {
        var model = this.get('model');
        model.deleteRecord();
        model.save();
      }
    }
  });

});